.miami-post {
  width: 370px;
  background: #fff;
  margin: 0 auto;
  border-radius: 3px;
  padding-bottom: 50px;
  position: relative;
}

.miami-post .header {
  text-align: right;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 250px;
  position: relative;
  overflow: hidden;
  background: #fff;
}

.miami-post .header .imgwrap {
  display: inline-block;
  width: 150px;
  height: 150px;
  background: #fff linear-gradient(to bottom right, rgba(var(--mclr), 0.5), rgba(var(--hclr), 0.5), rgba(var(--gclr), 0.5));
  padding: 1px;
  border-radius: 100%;
}

.miami-post .header .imgwrap img {
  width: 100%;
  height: 100%;
  border: 6px solid #fff;
  display: block;
  border-radius: 100%;
  object-fit: cover;
}

.miami-post .header .emoji {
  background-image: var(--ani);
  background-size: cover;
  width: 75px;
  height: 75px;
  display: inline-block;
}

.miami-post tline {
  background: #fff linear-gradient(to right, rgba(var(--mclr), 0.5), rgba(var(--hclr), 0.5), rgba(var(--gclr), 0.5));
  display: block;
  width: 100%;
  height: 1px;
}

.miami-post .emoji {
  margin: 0 auto;
  background-image: var(--ani);
  width: 50px;
  height: 50px;
  background-size: cover;
}

.miami-post .miami-post-content {
  background: #fff;
  padding: 50px;
  padding-top: 15px;
  font-size: 16px;
  text-align: justify;
  line-height: 165%;
  text-transform: lowercase;
}

.miami-post .miami-post-content b {
  border-radius: 2px;
  padding: 0 3px;
  color: #111;
  font-family: var(--font5);
  font-weight: 700;
  font-size: 17px;
  background: #fff linear-gradient(to right, rgba(var(--mclr), 0.2), rgba(var(--hclr), 0.2), rgba(var(--gclr), 0.2)) no-repeat left center/100% 10px;
}